import React from "react";
import {
  Row,
  Col,
  Card,
  Button,
} from "react-bootstrap";

import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";

import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import i18n from './../../../i18n';


var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
  // email: Yup.string().email("Invalid Email").required("Email is Required"),
  phone_number: Yup.string()
    .matches(phoneRegExp,i18n.t('error-phone-number'))
    .required(i18n.t('error-required-phone-number')),
  city: Yup.string()
    .min(2, i18n.t('error-short-governorate'))
    .max(20,i18n.t('error-long-governorate'))
    .required(i18n.t('error-required-governorate')),
  // country: Yup.string()
  //   .min(2, "Country is Too Short!")
  //   .max(20, "Country is Too Long!")
  //   .required("Country is Required"),
  name: Yup.string()
    .min(2,i18n.t('error-short-agency'))
    // .max(20,i18n.t('error-long-agency'))
    .required(i18n.t('error-required-agency')),
  address: Yup.string()
    .required(i18n.t('error-address')),
  // code: Yup.string()
  //   .min(2, "code is Too Short!")
  //   .max(20, "code is Too Long!")
  //   .required("code is Required"),
});
class AddAgency extends React.Component {
  state = {

    privileges: [],
    vehicules: [],
    showMessageParams:{
      display:false,
      type:"success",
      message:null,                
  },
  };

  componentDidMount() {


  }

  handlSubmit = (values) => {
    this.setState({ 
      showMessageParams:{
          display:false,
          type:"success",
          message:null
          
      }
  });
    console.log(values);
    console.log(localStorage.getItem('token'));
    
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer '+localStorage.getItem('token'),
      },
    };

    axios.post(urlbackend + "/addAgence ", values, config)
      .then(response => {
        this.setState({ 
          showMessageParams:{
              display:true,
              type:"success",
              message:i18n.t('success-add-agency')
          }
      });
        console.log(response.data)

      })
      .catch(e => {
        this.setState({ 
          showMessageParams:{
              display:true,
              type:"danger",
              message:i18n.t('error-add-agency')
          }
      });
        console.log(e)
      })
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    handleBlur,
    setFieldValue,
    isSubmitting,
    dirty,
    isValid,
    handleReset,
  }) => {
    // console.log(errors);
    const { showMessageParams } = this.state;
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">{i18n.t('add-agency')}</Card.Title>
              </Card.Header>
              {showMessageParams.display==true ?
              <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
              :null
              }
              <Card.Body>
                <Row>
                  <Col md={6}>
                    <CustomInput
                      name="name"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.name}
                      errors={errors.name}
                      touched={touched.name}
                      label={i18n.t('agency')}
                    />

                 
                      <CustomInput
                      name="phone_number"
                      label={i18n.t('phone-number')}
                      type="number"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.phone_number}
                      errors={errors.phone_number}
                      touched={touched.phone_number}
                    />

                 
                
                  </Col>
                  <Col md={6}>
                  
                 

             <CustomInput
                      name="city"
                      label={i18n.t('governorate')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.city}
                      errors={errors.city}
                      touched={touched.city}
                    />
                 

                 <CustomInput
                      name="address"
                      label={i18n.t('address')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.address}
                      errors={errors.address}
                      touched={touched.address}
                    />


                  </Col>
                </Row>

                <Button
                disabled={Object.keys(errors).length>0}  
                  onClick={() => {
                    this.handlSubmit(values);
                    handleReset();
                    // console.log(values);
                  }}
                >
                  {i18n.t('save')}
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  };

  render() {
    return (
      <Formik
        initialValues={{
          name: "",
          city: "",
          phone_number: "",
          address: "",
        }}
        onSubmit={(values, { setSubmitting }) => {
          this.submitForm(values);
          setSubmitting(false);
        }}
        validationSchema={SignupSchema}
      >
        {(props) => this.showForm(props)}
      </Formik>
    );
  }
}

export default AddAgency;
